@import '../../common.scss';

.navbar {
  background-color: $color-highlighted;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 0 30px;

  .title {
    color: $color-light-text;
    text-transform: none;
  }

  .items {
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    height: 100%;
  }

  .nav-item {
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 0 20px;
    height: 100%;
    color: $color-dark-text;
    text-transform: capitalize;
    font-weight: bold;

    &:hover {
      background-color: $color-highlighted-dark;
      color: white;
    }
  }

}