@import '../../common.scss';

.tribe-item {
  width: 130px;
  padding: 15px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  cursor: pointer;

  .item-title {
    text-align: center;
    margin-top: 5px;

    p {
      margin: 5px;
      text-transform: capitalize;
    }
  }

  &.in-progress {
    cursor:not-allowed;

    img {
      opacity: 0.5;
    }
  }

  &.in-progress:hover {
    background-color: inherit;
  }

  &:hover {
    background-color: $color-panel-background-dark;
  }

}
