@import '../../common.scss';

.empty-slot {
  padding: 0;
  margin: 15px;
  height: 100px;
  width: 100px;
  background-color: $color-dark-text-faded;
  border-radius: 10px;
}

.MuiBox-root {

  .cta {
    display: flex;
    margin: 15px 0;

    img {
      width: 40px;
      height: 40px;
      margin-right: 15px;
    }

    .btn-text {
      width: 100%;
    }

    .btn-title {
      margin-bottom: 5px;
    }

    .btn-details {
      font-weight: normal;
      width: 100%;
      display: flex;
      justify-content: space-between;

      span {
        display: block;
      }
    }
  }

}